<template>
  <v-card v-if="entregaSeleccionada" elevation="10" color="light-blue">
    <v-list-item two-line>
      <v-list-item-content>
        <v-list-item-subtitle>
          <v-btn rounded class="itemUnaEntrega" @click="generatedActions('clickEnVerMapa')"
                 :color="entregaSeleccionada.Latitud ===0 ? 'orange' : 'green darken-2' " block dark>
            Ver en el mapa
            <v-icon right>
              {{ entregaSeleccionada.Latitud === 0 ? 'mdi-map-marker-question-outline' : 'mdi-google-maps' }}
            </v-icon>
          </v-btn>
        </v-list-item-subtitle>
        <v-list-item-title>
          <v-chip class="ma-2 text-h6 itemUnaEntrega" label>
            Turno de entrega: # {{ entregaSeleccionada.OrdenEntrega }}
          </v-chip>
        </v-list-item-title>
      </v-list-item-content>
    </v-list-item>

    <v-list class="transparent">

      <v-list-item>
        <v-list-item-icon>
          <v-icon right color="white">mdi-receipt</v-icon>
        </v-list-item-icon>
        <v-list-item-subtitle class="white--text subtitle-1">
          Comprobante : <p class="text-h6"> {{ entregaSeleccionada.Comprobante }} </p>
        </v-list-item-subtitle>
      </v-list-item>

      <v-list-item>
        <v-list-item-icon>
          <v-icon right color="white">mdi-barcode-scan</v-icon>
        </v-list-item-icon>
        <v-list-item-subtitle class="white--text subtitle-1">Remitos :</v-list-item-subtitle>
      </v-list-item>
      <p class="overflow-visible white--text subtitle-1 ml-16 font-weight-bold">
        {{ entregaSeleccionada.Remitos }}
      </p>

      <v-list-item>
        <v-list-item-icon>
          <v-icon right color="white">mdi-account-circle</v-icon>
        </v-list-item-icon>
        <v-list-item-subtitle class="white--text subtitle-1">
          Cliente : <p class="text-h6">{{ entregaSeleccionada.NombreCliente }}</p>
        </v-list-item-subtitle>
      </v-list-item>

      <v-list-item>
        <v-list-item-icon>
          <v-icon right color="white">mdi-archive</v-icon>
        </v-list-item-icon>
        <v-list-item-subtitle class="white--text subtitle-1">
          N° Bultos : <p class="text-h6"> {{ entregaSeleccionada.Bultos }} </p>
        </v-list-item-subtitle>
      </v-list-item>

      <v-list-item>
        <v-list-item-icon>
          <v-icon right color="white">mdi-home-city</v-icon>
        </v-list-item-icon>
        <v-list-item-subtitle class="white--text subtitle-1">
          Destino : <p class="text-h6"> {{ entregaSeleccionada.NombreDestino }} </p>
        </v-list-item-subtitle>

      </v-list-item>

      <v-list-item>
        <v-list-item-icon>
          <v-icon right color="white">mdi-map-marker-radius-outline</v-icon>
        </v-list-item-icon>
        <v-list-item-subtitle class="white--text subtitle-1">
          Domicilio : <p class="text-h6"> {{ entregaSeleccionada.Domicilio }} </p>
        </v-list-item-subtitle>
      </v-list-item>

      <v-list-item>
        <v-list-item-icon>
          <v-icon right color="white">mdi-map-search-outline</v-icon>
        </v-list-item-icon>
        <v-list-item-subtitle class="white--text subtitle-1">
          Localidad :
        </v-list-item-subtitle>
      </v-list-item>
      <p class="overflow-visible white--text text-h6 ml-16">
        {{ entregaSeleccionada.Localidad }}
      </p>


    </v-list>
    <v-dialog v-model="noEntregado" fullscreen hide-overlay transition="dialog-bottom-transition">
      <v-card>
        <v-toolbar dark color="primary">
          <v-btn icon dark @click="closeFoto">
            <v-icon>mdi-close-circle</v-icon>
          </v-btn>
          <v-toolbar-title>Registrar visita</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn :disabled="!img" dark text @click="guardar">
              Guardar
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-list
            three-line
            subheader
        >
          <v-subheader>Fotografía del sitio visitado.</v-subheader>
          <v-list-item>
            <v-list-item-content>
              <v-list-item-subtitle>
                <v-file-input
                    v-model="imgCapture"
                    chips
                    :rules="[rules.required]"
                    prepend-icon="mdi-camera"
                    @change="imgTransform"

                ></v-file-input>
              </v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>
          <v-subheader class="error--text" v-if="error">{{ error }}</v-subheader>
        </v-list>
        <v-divider></v-divider>
        <v-list v-show="img" three-line subheader>
          <v-subheader>Visualizacion</v-subheader>
          <v-col cols="12">
            <v-card elevation="10" shaped>
              <v-img
                  :src="img"
                  aspect-ratio="1.7"
                  max-height="300"
                  class="grey darken-4"
              ></v-img>
            </v-card>
          </v-col>


        </v-list>

      </v-card>
    </v-dialog>


    <v-divider></v-divider>
    <v-card-actions>
      <v-row class="mx-4">
        <v-btn @click="generatedActions('clickEnOk')" class="mt-6 my-2" color="green" block dark>
          OK
          <v-icon right>mdi-checkbox-marked-circle</v-icon>
        </v-btn>
        <v-btn @click="noEntregado = true " class="my-2" color="red" block dark>
          NO
          <v-icon right>mdi-close-octagon</v-icon>
        </v-btn>

        <v-btn @click="generatedActions('clickEnParcial')" class="my-2" color="orange" block dark>
          Parcial
          <v-icon right>mdi-progress-check</v-icon>
        </v-btn>

      </v-row>
    </v-card-actions>
    <v-card-actions class="mx-4">
      <v-btn @click="generatedActions('clickEnCancelar')" color="rgb(37, 150, 190)" block dark>
        <v-icon left>mdi-arrow-left-circle</v-icon>
        Volver a la lista
      </v-btn>
    </v-card-actions>

  </v-card>
</template>
<script>
import choferes from "../store/choferes";

export default {
  name: 'card-confirm-entrega',
  props: {
    entregaSeleccionada: {}
  },
  data: () => ({
    noEntregado: false,
    imgCapture: null,
    img: null,
    error: '',
    rules: {required: value => !!value || 'Campo requerido.'}
  }),

  watch: {},

  computed: {},

  methods: {
    clear() {
      this.imgCapture = {}
      this.img = ''
    },
    generatedActions(action) {
      this.$emit(`${action}`)
    },
    imgTransform() {
      this.error = ''
      if (this.imgCapture) {
        if (!(/\.(jpg|png)$/i).test(this.imgCapture.name)) {
          this.error = 'No corresponde a una imagen'
          return
        }
        this.img = URL.createObjectURL(this.imgCapture)

      } else {
        this.img = null
      }

    },
    async guardar(){
      const {Id} = this.entregaSeleccionada
      try {
        this.$store.dispatch("loading/mostrar", "Procesando fotografía...")
        const hash = await this.$store.dispatch("choferes/getHashByPhoto", { Id, fileItem: this.imgCapture} )
        this.$store.dispatch("loading/ocultar")
        if (hash.hasOwnProperty('resultAWS') && hash.hasOwnProperty('data')) {
          
          await this.$store.dispatch("alertDialog/mostrar",
              {
                titulo: 'Éxito!',
                botonPrimario: "Aceptar",
                mensaje: 'El documento ha sido registrado exitosamente',
  
              })
          this.$emit('clickEnNo')
          this.closeFoto()
        }
      } catch (error) {
        console.log("Error en hash", error);
        this.$store.dispatch("loading/ocultar")
        this.$store.dispatch("snackbar/mostrar", error)
      }


    },
    closeFoto() {
      this.imgCapture = null
      this.img= null
      this.noEntregado = false
    }
  }
}
</script>
<style scoped>
.itemUnaEntrega {
  font-weight: 900;
  font-family: "Helvetica Neue";
}
</style>
